import { IRoute } from "@src/router/routes";

/**
 * id
 * @param {IRoute[]} datas 
 * @param {string} value 
 * @param {keyof IRoute} field 
 */
export function getGroupById(
  datas: IRoute[],
  value: number | string,
  field: keyof IRoute
) {
  let hasFound = false; // id
  let result: any;
  let fn = function (datas: IRoute[]) {
    if (Array.isArray(datas) && !hasFound) {
      // 
      for (let i = 0; i < datas.length; i++) {
        if (datas[i][field] === value) {
          result = datas[i];
          hasFound = true;
          break;
        } else if (datas[i].children) {
          fn(datas[i].children as IRoute[]);
        }
      }
    }
  };
  fn(datas);
  return result;
}

/**
 * id
 * @param {IRoute[]} data
 * @param {string} id
 */
export function getParentsById(data: IRoute[], id: string): any {
  for (const i in data) {
    if (data[i].children) {
      let ro = getParentsById(data[i].children as IRoute[], id);
      if (ro !== undefined) {
        // subResource
        const index = (data[i].children as IRoute[]).findIndex(
          (item: any) => item.meta.menu
        );
        // 
        if (index !== -1) {
          return ro.concat({
            path: (data[i].children as IRoute[])[index].path,
            name: data[i].title,
          });
        }
        // path
        return ro.concat({
          path: data[i].path,
          name: data[i].title,
        });
      }
    }
    // path
    if (data[i].path === id) {
      return [
        {
          path: "",
          name: data[i].title,
        },
      ];
    }
  }
}
