import axios, { AxiosResponse, CancelTokenSource } from "axios";
import Qs from "qs";

const NODE_ENV = process.env.NODE_ENV;

export interface IBaseReponse<T> {
  // 
  success: boolean;
  // 
  data: T;
}

console.log(NODE_ENV);

axios.defaults.baseURL =
  NODE_ENV === "development"
    ? window.CONFIG.developmentApi
    : window.CONFIG.productionApi;

// 
const requestList: string[] = [];
/**
 * 
 * @param url -url
 * @param cancel -
 */
const stopRepeatRequest = function (
  url: string,
  source: CancelTokenSource
): void {
  for (let i = 0, len = requestList.length; i < len; i++) {
    if (requestList[i] === url) {
      source.cancel();
      return;
    }
  }
  requestList.push(url);
};

/**
 * 
 * @param url -url
 */
const allowRequest = function (url: string): void {
  for (let i = 0, len = requestList.length; i < len; i++) {
    if (requestList[i] === url) {
      requestList.splice(i, 1);
      break;
    }
  }
};

// 
axios.interceptors.request.use(
  (config) => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    // token
    if (typeof config.headers === "object") {
      config.headers.Authorization = `Bearer ${123}`;
    }
    // 
    config.cancelToken = source.token;

    stopRepeatRequest(
      `${config.url}?${Qs.stringify(config.params)}&method=${config.method}`,
      source
    );

    return config;
  },
  (error) => {
    // 
    return Promise.reject(error);
  }
);

// 
axios.interceptors.response.use(
  (response: AxiosResponse<any, any>) => {
    // 2xx 
    // 
    const { config, status } = response;
    if (config) {
      allowRequest(
        `${config.url}?${Qs.stringify(config.params)}&method=${config.method}`
      );
    }
    if (status !== 200) {
      throw new Error(", HTTP  200");
    }

    return response.data;
  },
  (error) => {
    const { config } = error;
    if (config) {
      allowRequest(
        `${config.url}?${Qs.stringify(config.params)}&method=${config.method}`
      );
    }
    //  2xx 
    // 
    return Promise.reject(error);
  }
);

export default axios;
